import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
import axios from "axios";
import router  from "@/routes/index";

const store = new Vuex.Store({
    strict:true,
    state: {
        // 
        userInfo:{},
        // 
        sibarMenu:[],
        // 
        dialogVisible:false,
    },
    mutations: {
        // 
        setUserInfo (state,payLoad){
            state.userInfo = payLoad.userInfo;

        },
        setDialogVisible(state,payLoad){
            state.dialogVisible = payLoad.dialogVisible;

        },
        setSibarMenu (state,payLoad){
            state.sibarMenu = payLoad.sibarMenu;

        }
        
    },
    actions:{
        // 
        async getUserInfo ({commit}){
            try {
                
                let res = await axios.get("/admin/userInfo");
                commit("setUserInfo",{
                    userInfo : res.data,
                });

            } catch (error) {
                console.log(error);
            }
        },
        // 
        async getSibarMenu ({commit}){
            try {
                let res = await axios.get("/admin/menu", {
                    params: {
                        // 
                        type: 1,
                    },
                });
                commit("setSibarMenu",{
                    sibarMenu:res.data
                });
            } catch (error) {
                console.log(error);
            }

        }
       
        
    }



});

export default store;