<template>
  <div class="sideBar">
    <el-menu
      class="menu"
      :default-active="$route.path"
      router
      background-color="#253040"
      text-color="#FFFFFF"
      :unique-opened="true"
      menu-trigger="click"
    >
      <template v-for="(item, index) of list">
        <template v-if="item._child && item._child.length">
          <el-submenu
            :key="index"
            :index="item.url"
          >
            <template
              :index="item.url"
              slot="title"
            >
              <span>{{ item.name }}</span>
            </template>
            <el-menu-item-group class="child">
              <SideBar :list="item._child" />
            </el-menu-item-group>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item
            :key="index"
            :index="item.url"
          >
            <span>{{ item.name }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
export default {
    name: "SideBar",
    data() {
        return {
        };
            
    },
    props: {
    // eslint-disable-next-line vue/require-default-prop
        list: Array,
    },
    
    components: {},
};
</script>

<style lang="scss" scoped>
.menu{
  width: 100%;
  overflow: hidden;
}

.child{
  box-sizing: border-box;
  padding-left: 15px;
}
.el-menu-item.is-active {
      background-color: #f7961c !important;
      color: #fff;
      span {
        color: #fff !important;
      }

}
</style>
