<template>
  <div class="index-container" @click="isLogoutShow = false">
    <header class="header">
      <img class="logo" src="@/static/images/logo.png">
      <div class="userInfo">
        <div class="userImgContainer">
          <img class="user-img" :src="userInfo.avater">
        </div>
        <span class="userName">{{ userInfo.user_name }}</span>
        <span class="iconfont icon-xiala icon" />
        <div class="logout" @click.stop="logout">
          <span></span>
        </div>
      </div>
    </header>
    <div class="content">
      <div class="menu">
        <SideBar :list="sibarMenu" />
      </div>
      <div class="menu-item">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="(item, index) of breadList" :key="index">
            {{ item.meta.name }}
          </el-breadcrumb-item>
        </el-breadcrumb>
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import SideBar from "./sideBar.vue";
import { mapState } from "vuex";
export default {
    data() {
        return {
            isLogoutShow: false,
            // 
            breadList: [],
            // 
            menuData: [],
            value1: "",
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                },
            },
           
        };
    },
    // 
    beforeRouteEnter(to, from, next) {
        if (to.matched) {
            let matched = to.matched.filter((item) => item.meta && item.meta.name);
            next((vm) => {
                vm.breadList = matched;
            });
        }
    },
    watch: {
    // 
        $route(value) {
            if (value.matched) {
                let matched = value.matched.filter(
                    (item) => item.meta && item.meta.name
                );
                this.breadList = matched;
            }
        },
    },
    computed:{
        ...mapState([
            "userInfo",
            "sibarMenu"
        ])

    },
    methods: {
    // 
        logout() {
            this.isLogoutShow = false;
            this.$router.replace({
                name: "login",
            });
            localStorage.removeItem("token");
            this.$store.commit("setUserInfo", {
                userInfo: {},
            });
        },
       

    },

    components: {
        SideBar,
    },
};
</script>

<style lang="scss" scoped>
.index-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: rgba(220, 220, 220, 0.5);
  .header {
    height: 75px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    .logo {
      height: 100%;
      width: 220px;
    }
    .userInfo {
      padding: 0 10px;
      display: flex;
      align-items: center;
      position: relative;
      cursor: pointer;
      &:hover{
        .logout{
          visibility: visible;
          opacity: 1;
          top: 47px;
        }

      }
      .userImgContainer {
        width: 47px;
        height: 47px;
        overflow: hidden;
        border-radius: 50%;
        .user-img{
          width: 100%;
          height: 100%;
        }
      }
      .userName {
        margin-left: 10px;
        color: rgba(102, 102, 102, 1);
      }
      .icon {
        font-size: 13px;
        color: rgba(102, 102, 102, 1);
        margin-left: 7px;
      }
      .logout {
        position: absolute;
        visibility: hidden;
        opacity: 0;
        width: 100%;
        box-sizing: border-box;
        left: 0;
        text-align: center;
        top: 70px;
        padding: 10px;
        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.5) inset;
        background: white;
        transition: all 0.5s ease-in;
        &::after{
          content: "";
          position: absolute;
          left: 50%;
          top: 0;
          transform-origin: center;
          transform: translate(-50%,-50%) rotate(45deg);
          width: 15px;
          height: 15px;
          background: white;
          border-left: 1px solid rgba(0, 0, 0, 0.5);
          border-top: 1px solid rgba(0, 0, 0, 0.5);
        }
      }
    }
  }
  .content {
    margin-top: 5px;
    flex: 1;
    display: flex;
    background: rgba(220, 220, 220, 0.5);
    .menu {
      width: 170px;
      height: 100%;
      overflow: hidden;
      color: white;
      background: rgba(37, 48, 64, 1);
      user-select: none;
    }
    .menu-item {
      width: calc(100% - 170px);
      box-sizing: border-box;
      margin-bottom: 10px;
      margin-left: 10px;
      padding: 10px 22px;
      background: white;
      .breadcrumb {
        margin: 0 0 17px 0;
        font-size: 17px;
      }
    }
  }
}
</style>
